.wrapper {
  text-align: center;
  margin: 20px 10px;
  padding: 0px;
  max-width: 23%;

  &:hover {
    box-shadow: 0px 0px 5px 0px lightgrey;
    transition: 0.3s;
  }
  a {
    text-decoration: none;
    color: black;
  }
  h4 {
    margin-top: 10px;
  }
}
img {
  width: 100%;
}
